/* Publication.css */
.publications {
    padding: 80px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.publication-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.publication-card {
    /* border: 1px solid #ddd; */
    margin-bottom: 20px;
    padding: 20px;
    transition: all 0.2s ease-in-out;
    width: 90%;  /* You can adjust this value as per your design */
    box-sizing: border-box;
    border-radius: 15px;  /* Rounded corners */
    box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.1); /* Initial shadow */
}

.publication-card:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
}

.publication-card a {
    text-decoration: none;
    color: inherit;
}

.publication-card p {
    margin: 0;
}
