.Experience {
    margin: 20px;
    padding: 20px;
    box-sizing: border-box;
}

.title {
    font-size: 1.5em; 
    text-align: center;
    color: #333;
    margin-bottom: 1em;
}

.experience-content {
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.experience-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2em;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 90%;
    transition: transform 0.3s ease-in-out;
}

.experience-card:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */

}

.company-logo {
    width: 100px;
    height: 100px;
    margin-bottom: 1em;
}

.experience-details h3, .experience-details h4 {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 0.5em;
    text-align: center;
}

.experience-details p {
    color: #555;
    margin-bottom: 0.5em;
    text-align: left; /* Changed to left-align the text blocks in <p> */
}

/* Center the date */
.experience-details p strong {
    display: block;
    text-align: center;
}
