/* Research.css */
.research {
    padding: 80px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.research-content {
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;  /* Add this line */
    width: 100%;
}

.research-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2em;
    background: #fff;
    border-radius: 10px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 90%;
    transition: transform 0.3s ease-in-out;
}

.research-card:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.2); /* Darker shadow on hover */
}

.research-details h3, .research-details h4 {
    font-size: 1.2em;
    color: #333;
    margin-bottom: 0.5em;
    text-align: center;
}

.research-details p {
    color: #555;
    margin-bottom: 0.5em;
    text-align: center;
}
