.Education {
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.education-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.education-card {
  border: 1px solid #ddd;
  margin-bottom: 20px;
  padding: 20px;
  transition: all 0.2s ease-in-out;
  width: 90%;
  box-sizing: border-box;
  border-radius: 15px;
  box-shadow: 0 10px 30px -10px rgba(0, 0, 0, 0.1);
}

.education-card:hover {
  transform: scale(1.05);
  box-shadow: 0 10px 30px -5px rgba(0, 0, 0, 0.2);
}

.education-card:hover {
  transform: scale(1.05);
}

.title {
font-size: 1.5em;
text-align: center;
color: #333;
margin-bottom: 1em;
}

.education-content h3 {
font-size: 1.2em;
color: #333;
margin-bottom: 0.5em;
}

.education-content p, .education-content ul {
color: #555;
margin-bottom: 0.5em;
}

.education-content ul {
padding-left: 1em;
}

.education-content ul li {
margin-top: 10px;
}

.school-logo {
width: 100px; /* Adjust size as needed */
height: auto;
margin-bottom: 20px;
}
