/* Navbar.css */
.Navbar {
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    padding: 20px 0;
    background-color: white;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2); /* Add this line */
    z-index: 1000;
  }
  
  .Link {
    cursor: pointer;
    text-decoration: none;
    color: black;
    transition: color 0.3s ease;
  }
  
  .Link:hover {
    color: grey; /* Add this line */
  }
  